.cover.active {
  background-color: rgba(54, 54, 172, 0.418);
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}

.notes {
  width: 70%;
  margin: 3rem 0rem;
}

.notes .grid {
  display: -ms-grid;
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-grid-columns: (minmax(200px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: minmax(150px, auto);
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
}

.notes .searchDivider {
  margin-top: 2rem;
  border-bottom: 2px solid #8686fc;
  display: inline-block;
  padding-bottom: .5rem;
}

.notes .searchDivider:nth-child(1) {
  margin-top: 0;
}
