nav.nav {
  background-color: #8686fc;
  height: 5rem;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

nav.nav .search {
  width: 30rem;
  max-width: 80%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}

nav.nav .search span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #7070ff;
  position: absolute;
  right: .5rem;
  border-radius: 50%;
  padding: .3rem;
}

nav.nav .search span svg {
  height: 1.7rem;
  width: 1.7rem;
}

nav.nav .search span:hover {
  background-color: rgba(0, 0, 0, 0.13);
}

nav.nav .search input {
  border-radius: 0.4rem;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 0.8rem 0rem 0.8rem 2rem;
  font-size: 1.3rem;
  width: 100%;
  outline: none;
  background-color: #c3c3ec;
  border: none;
  color: #8686fc;
}

nav.nav .search input:focus {
  background-color: #fff;
}

nav.nav .search input::-webkit-input-placeholder {
  color: #8686fc;
}

nav.nav .search input:-ms-input-placeholder {
  color: #8686fc;
}

nav.nav .search input::-ms-input-placeholder {
  color: #8686fc;
}

nav.nav .search input::placeholder {
  color: #8686fc;
}
