.colorbtn .color,
.colorbtn {
  height: 1.6rem;
  width: 1.6rem;
  border-radius: 50%;
  position: relative;
  border: none;
}

.colorbtn::before {
  visibility: hidden;
  position: absolute;
  content: "";
  top: -1.1rem;
  border-top: 1rem solid #7070ff88;
  border-left: 0.7rem solid transparent;
  border-right: 0.7rem solid transparent;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.colorbtn::after {
  visibility: hidden;
  position: absolute;
  content: "";
  width: 350%;
  height: 1.5rem;
  bottom: 100%;
  left: -100%;
}

.colorbtn:hover::After, .colorbtn:hover::before {
  visibility: visible;
}

.colorbtn .menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  z-index: 50;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 10rem;
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  background-color: #7070ff88;
  border-radius: 0.5rem;
  padding: 0.4rem;
  position: absolute;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  bottom: 3rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
}

.colorbtn .menu .color {
  margin: 0.2rem;
  color: #3f3f3f;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  border: 2px solid transparent;
  -webkit-filter: drop-shadow(0px 0px 0px #000);
          filter: drop-shadow(0px 0px 0px #000);
}

.colorbtn .menu .color:hover {
  border: 2px solid #3a3a3a;
}

.colorbtn:hover .menu {
  opacity: 1;
  visibility: visible;
}
